const newStr = str.replace(`${data.abbreviation}`, `${data.name}`);
console.log(newStr);

function Beds({ beds }) {
	const [search, setSearch] = useState('');
	const [filterBeds, setFilterBeds] = useState([]);
	useEffect(() => {
		if (beds.length) {
			setFilterBeds(
				beds
					.filter((staffed) => {
						console.log(staffed);
						let lower = staffed.state.toLowerCase();
						let lowerSearch = search.toLowerCase();
						console.log(lower);
						return lower.indexOf(lowerSearch) !== -1;
					})
					.sort((firstEl, secondEl) => {
						if (firstEl.state < secondEl.state) {
							return -1;
						}
						if (firstEl.state > secondEl.state) {
							return 1;
						}
						return 0; 
						
					})
					
					
					);
					let firstArray = data;
					let secondArray = beds;
	
					let map = {};
					firstArray.forEach(i => map[i] = false);
					secondArray.forEach(i => map[i] === false && (map[i] = true));
					let jsonArray = Object.keys(map).map(k => ({ name: k, matched: map[k] }));
						return jsonArray;
				}
	}, [beds, search]);

	if (!filterBeds) {
		return <p>Loading state data...</p>;
	} else
		return (
			<section className='container'>
				<input
					type='text'
					placeholder='type/click state abbreviation'
					onChange={(e) => setSearch(e.target.value)}
				/>

				{filterBeds.map((staffed) => (
					<Link to={`/staffed/${staffed.state}`} key={staffed.state}>
						<div>
							<p>{staffed.state}</p>
						</div>
					</Link>
				))}
			</section>
		);
}
export default Beds;
